import React from 'react';
import { Section, Container, Text, Tag } from '../atoms';
import { ArrowRight } from 'lucide-react';
import styles from './Experience.module.css';

export interface ExperienceItem {
  id: string;
  company: string;
  role: string;
  date: string;
  location: string;
  type: string;
  description: string;
  responsibilities: string[];
  tags: string[];
}

export interface ExperienceProps {
  sectionNumber?: string;
  experiences: ExperienceItem[];
}

export const Experience: React.FC<ExperienceProps> = ({
  sectionNumber = '02',
  experiences,
}) => {
  return (
    <Section id="experience" className={styles.experience}>
      <Container>
        <div className={styles.experienceHeader}>
          <Text variant="h2" className={styles.sectionNumber}>
            {sectionNumber}
          </Text>
          <Text variant="h2">Experience</Text>
        </div>

        <div className={styles.timeline}>
          <div className={styles.timelineLine} />
          <div className={styles.timelineItems}>
            {experiences.map((exp) => (
              <div key={exp.id} className={styles.timelineItem}>
                <div className={styles.timelineDotOutline} />
                <div className={styles.timelineDot} />
                <div className={styles.experienceCard}>
                  <div className={styles.experienceHeader}>
                    <div>
                      <Text variant="h4" className={styles.companyName}>
                        {exp.company}
                      </Text>
                      <Text variant="h5" className={styles.role}>
                        {exp.role}
                      </Text>
                      <Text variant="bodySmall" color="tertiary">
                        {exp.location} • {exp.type}
                      </Text>
                    </div>
                    <Text variant="bodySmall" color="tertiary" className={styles.dateLocation}>
                      {exp.date}
                    </Text>
                  </div>
                  <Text variant="body" color="secondary" className={styles.experienceDescription}>
                    {exp.description}
                  </Text>
                  <ul className={styles.experienceList}>
                    {exp.responsibilities.map((responsibility, index) => (
                      <li key={index} className={styles.experienceListItem}>
                        <ArrowRight size={16} className={styles.experienceListArrow} />
                        <span>{responsibility}</span>
                      </li>
                    ))}
                  </ul>
                  <div className={styles.experienceTags}>
                    {exp.tags.map((tag) => (
                      <Tag key={tag}>{tag}</Tag>
                    ))}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </Container>
    </Section>
  );
};

