import React from 'react';
import { Section, Container, Text, Tag } from '../atoms';
import { ExternalLink } from 'lucide-react';
import styles from './Work.module.css';

export interface Project {
  id: string;
  number: string;
  year: string;
  category: string;
  title: string;
  description: string;
  tags: string[];
  link?: string;
  linkLabel?: string;
  visualVariant?: 'default' | 'alt' | 'alt2';
}

export interface WorkProps {
  sectionNumber?: string;
  projects: Project[];
}

export const Work: React.FC<WorkProps> = ({
  sectionNumber = '01',
  projects,
}) => {
  return (
    <Section id="work" className={styles.work}>
      <Container>
        <div className={styles.workHeader}>
          <Text variant="h2" className={styles.sectionNumber}>
            {sectionNumber}
          </Text>
          <Text variant="h2">Work</Text>
        </div>

        <div className={styles.projectsGrid}>
          {projects.map((project, index) => (
            <div
              key={project.id}
              className={`${styles.projectCard} ${
                index % 2 === 1 ? styles.projectCardReverse : ''
              }`}
            >
              <div className={styles.projectInfo}>
                <div className={styles.projectMeta}>
                  <span className={styles.projectYear}>{project.year}</span>
                  <span>•</span>
                  <span className={styles.projectCategory}>{project.category}</span>
                </div>
                <Text variant="h3" className={styles.projectTitle}>
                  {project.title}
                </Text>
                <Text variant="body" color="secondary" className={styles.projectDescription}>
                  {project.description}
                </Text>
                <div className={styles.projectTags}>
                  {project.tags.map((tag) => (
                    <Tag key={tag}>{tag}</Tag>
                  ))}
                </div>
                {project.link && (
                  <a
                    href={project.link}
                    target="_blank"
                    rel="noopener noreferrer"
                    className={styles.projectLink}
                  >
                    {project.linkLabel || 'View Project'}
                    <ExternalLink size={16} />
                  </a>
                )}
              </div>
              <div
                className={`${styles.projectVisual} ${
                  project.visualVariant === 'alt'
                    ? styles.projectVisualAlt
                    : project.visualVariant === 'alt2'
                    ? styles.projectVisualAlt2
                    : ''
                }`}
              />
            </div>
          ))}
        </div>
      </Container>
    </Section>
  );
};

